
'use client';

import React, { useState, useEffect, useMemo } from 'react'; // Re-triggering IDE re-index
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Plus, Trash2, Calculator as CalcIcon, TrendingUp, Target, Save, RotateCcw, Info } from 'lucide-react';
import { Course, DegreeDomain, Grade, GRADE_POINTS, calculateCGPA, predictCGPA, DEFAULT_COURSES } from '@/lib/grading';
import { motion, AnimatePresence } from 'framer-motion';
import { toast } from 'sonner';

export default function Calculator() {
    const [domain, setDomain] = useState<DegreeDomain>('Data Science');
    const [courses, setCourses] = useState<Course[]>([]);
    const [newCourse, setNewCourse] = useState<Partial<Course>>({ name: '', credits: 4, grade: 'S' });
    const [isInitialized, setIsInitialized] = useState(false);

    // Load from localStorage on mount
    useEffect(() => {
        const saved = localStorage.getItem('iitm-cgpa-data');
        if (saved) {
            try {
                const parsed = JSON.parse(saved);
                setCourses(parsed.courses || []);
                setDomain(parsed.domain || 'Data Science');
            } catch (e) {
                console.error('Failed to load storage', e);
            }
        } else {
            // Pre-fill with default courses for the domain
            const defaults = DEFAULT_COURSES[domain].map((c) => ({
                ...c,
                id: Math.random().toString(36).substr(2, 9),
                grade: 'S' as Grade,
                isOngoing: false
            })) as Course[];
            setCourses(defaults);
        }
        setIsInitialized(true);
    }, []); // eslint-disable-next-line react-hooks/exhaustive-deps

    // Save to localStorage
    useEffect(() => {
        if (isInitialized) {
            localStorage.setItem('iitm-cgpa-data', JSON.stringify({ courses, domain }));
        }
    }, [courses, domain, isInitialized]);

    const stats = useMemo(() => {
        const current = calculateCGPA(courses.filter(c => !c.isOngoing));
        const predicted = predictCGPA(courses.filter(c => !c.isOngoing), courses.filter(c => c.isOngoing));
        return { current, predicted };
    }, [courses]);

    const addCourse = (type: 'completed' | 'ongoing') => {
        if (!newCourse.name) {
            toast.error('Please enter a course name');
            return;
        }
        const course: Course = {
            id: Math.random().toString(36).substr(2, 9),
            name: newCourse.name,
            credits: Number(newCourse.credits) || 0,
            grade: (newCourse.grade as Grade) || 'S',
            isOngoing: type === 'ongoing'
        };
        setCourses([...courses, course]);
        setNewCourse({ name: '', credits: 4, grade: 'S' });
        toast.success(`${type === 'completed' ? 'Completed' : 'Ongoing'} course added`);
    };

    const removeCourse = (id: string) => {
        setCourses(courses.filter(c => c.id !== id));
        toast.info('Course removed');
    };

    const updateCourse = (id: string, updates: Partial<Course>) => {
        setCourses(courses.map(c => c.id === id ? { ...c, ...updates } : c));
    };

    const resetData = () => {
        if (confirm('Are you sure you want to reset all data?')) {
            const defaults = DEFAULT_COURSES[domain].map((c) => ({
                ...c,
                id: Math.random().toString(36).substr(2, 9),
                grade: 'S' as Grade,
                isOngoing: false
            })) as Course[];
            setCourses(defaults);
            localStorage.removeItem('iitm-cgpa-data');
            toast.success('Data reset to defaults');
        }
    };

    if (!isInitialized) return null;

    return (
        <div className="space-y-8 max-w-6xl mx-auto p-4 md:p-8">
            {/* Header Section */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4 bg-white/50 backdrop-blur-md p-6 rounded-2xl border border-green-100 shadow-sm">
                <div>
                    <h1 className="text-3xl font-bold text-green-900 flex items-center gap-2">
                        <CalcIcon className="w-8 h-8 text-green-600" />
                        IITM BS CGPA Dashboard
                    </h1>
                    <p className="text-slate-600 mt-1">Plan your academic journey in {domain}</p>
                </div>
                <div className="flex gap-2">
                    <Select value={domain} onValueChange={(v: DegreeDomain) => {
                        setDomain(v);
                        if (confirm(`Switching to ${v} will reset your current course list. Continue?`)) {
                            const defaults = DEFAULT_COURSES[v].map(c => ({
                                ...c,
                                id: Math.random().toString(36).substr(2, 9),
                                grade: 'S' as Grade,
                                isOngoing: false
                            })) as Course[];
                            setCourses(defaults);
                        }
                    }}>
                        <SelectTrigger className="w-[200px] border-green-200 focus:ring-green-500">
                            <SelectValue placeholder="Select Degree" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="Data Science">BS in Data Science</SelectItem>
                            <SelectItem value="Electronic Systems">BS in Electronic Systems</SelectItem>
                        </SelectContent>
                    </Select>
                    <Button variant="outline" size="icon" onClick={resetData} className="border-red-100 text-red-600 hover:bg-red-50">
                        <RotateCcw className="w-4 h-4" />
                    </Button>
                </div>
            </div>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.1 }}>
                    <Card className="bg-gradient-to-br from-green-50 to-white border-green-100 overflow-hidden relative">
                        <div className="absolute -right-4 -top-4 opacity-5">
                            <CalcIcon size={120} />
                        </div>
                        <CardHeader className="pb-2">
                            <CardTitle className="text-sm font-medium text-green-700 flex items-center gap-2">
                                <Target className="w-4 h-4" /> Current CGPA
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="text-4xl font-bold text-green-950">{stats.current.cgpa}</div>
                            <p className="text-xs text-green-600 mt-1">{stats.current.totalCredits} Credits Completed</p>
                        </CardContent>
                    </Card>
                </motion.div>

                <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.2 }}>
                    <Card className="bg-gradient-to-br from-emerald-50 to-white border-emerald-100 overflow-hidden relative">
                        <div className="absolute -right-4 -top-4 opacity-5">
                            <TrendingUp size={120} />
                        </div>
                        <CardHeader className="pb-2">
                            <CardTitle className="text-sm font-medium text-emerald-700 flex items-center gap-2">
                                <TrendingUp className="w-4 h-4" /> Predicted CGPA
                            </CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="text-4xl font-bold text-emerald-950">{stats.predicted.predictedCgpa}</div>
                            <p className="text-xs text-emerald-600 mt-1">Including {courses.filter(c => c.isOngoing).length} Ongoing Subjects</p>
                        </CardContent>
                    </Card>
                </motion.div>

                <motion.div initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ delay: 0.3 }}>
                    <Card className="bg-gradient-to-br from-blue-50 to-white border-blue-100 overflow-hidden relative">
                        <div className="absolute -right-4 -top-4 opacity-5">
                            <Info size={120} />
                        </div>
                        <CardHeader className="pb-2">
                            <CardTitle className="text-sm font-medium text-blue-700">Total Credits</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="text-4xl font-bold text-blue-950">{stats.predicted.totalCredits}</div>
                            <p className="text-xs text-blue-600 mt-1">Points: {stats.predicted.totalGradePoints}</p>
                        </CardContent>
                    </Card>
                </motion.div>
            </div>

            {/* Main Tabs */}
            <Card className="border-green-100 shadow-xl shadow-green-900/5">
                <CardContent className="p-0">
                    <Tabs defaultValue="completed" className="w-full">
                        <div className="px-6 pt-6 bg-slate-50/50 border-b border-green-50">
                            <TabsList className="bg-green-100/50 p-1 mb-4">
                                <TabsTrigger value="completed" className="data-[state=active]:bg-white data-[state=active]:text-green-700">6.1 Current CGPA</TabsTrigger>
                                <TabsTrigger value="ongoing" className="data-[state=active]:bg-white data-[state=active]:text-green-700">6.2 Prediction</TabsTrigger>
                                <TabsTrigger value="planner" className="data-[state=active]:bg-white data-[state=active]:text-green-700">6.3 Future Planner</TabsTrigger>
                            </TabsList>
                        </div>

                        <TabsContent value="completed" className="p-6 focus-visible:ring-0">
                            <div className="flex flex-col gap-6">
                                <div className="flex flex-col md:flex-row gap-4 p-4 bg-green-50/30 rounded-lg border border-green-100">
                                    <div className="flex-1 space-y-2">
                                        <Label htmlFor="c-name">Course Name</Label>
                                        <Input id="c-name" value={newCourse.name} onChange={e => setNewCourse({ ...newCourse, name: e.target.value })} className="bg-white" />
                                    </div>
                                    <div className="w-full md:w-24 space-y-2">
                                        <Label htmlFor="c-credits">Credits</Label>
                                        <Input id="c-credits" type="number" value={newCourse.credits} onChange={e => setNewCourse({ ...newCourse, credits: parseInt(e.target.value) })} className="bg-white" />
                                    </div>
                                    <div className="w-full md:w-32 space-y-2">
                                        <Label htmlFor="c-grade">Grade</Label>
                                        <Select value={newCourse.grade} onValueChange={v => setNewCourse({ ...newCourse, grade: v as Grade })}>
                                            <SelectTrigger className="bg-white"><SelectValue /></SelectTrigger>
                                            <SelectContent>
                                                {Object.keys(GRADE_POINTS).map(g => (
                                                    <SelectItem key={g} value={g}>{g}</SelectItem>
                                                ))}
                                            </SelectContent>
                                        </Select>
                                    </div>
                                    <div className="flex items-end">
                                        <Button onClick={() => addCourse('completed')} className="bg-green-700 hover:bg-green-800 text-white w-full md:w-auto"><Plus className="w-4 h-4 mr-2" /> Add</Button>
                                    </div>
                                </div>
                                <div className="border rounded-xl bg-white overflow-x-auto">
                                    <Table>
                                        <TableHeader className="bg-slate-50">
                                            <TableRow><TableHead>Course Name</TableHead><TableHead>Credits</TableHead><TableHead>Grade</TableHead><TableHead className="text-right">Action</TableHead></TableRow>
                                        </TableHeader>
                                        <TableBody>
                                            <AnimatePresence mode="popLayout">
                                                {courses.filter(c => !c.isOngoing).map(course => (
                                                    <motion.tr
                                                        key={course.id}
                                                        initial={{ opacity: 0, x: -20 }}
                                                        animate={{ opacity: 1, x: 0 }}
                                                        exit={{ opacity: 0, x: 20 }}
                                                        className="group border-b last:border-0 hover:bg-slate-50/50 transition-colors"
                                                    >
                                                        <TableCell className="font-medium py-3">{course.name}</TableCell>
                                                        <TableCell>{course.credits}</TableCell>
                                                        <TableCell>
                                                            <Select value={course.grade} onValueChange={v => updateCourse(course.id, { grade: v as Grade })}>
                                                                <SelectTrigger className="w-20 h-8 text-xs bg-white"><SelectValue /></SelectTrigger>
                                                                <SelectContent>{Object.keys(GRADE_POINTS).map(g => (<SelectItem key={g} value={g}>{g}</SelectItem>))}</SelectContent>
                                                            </Select>
                                                        </TableCell>
                                                        <TableCell className="text-right">
                                                            <Button variant="ghost" size="icon" onClick={() => removeCourse(course.id)} className="text-red-400 hover:text-red-600 hover:bg-red-50"><Trash2 className="w-4 h-4" /></Button>
                                                        </TableCell>
                                                    </motion.tr>
                                                ))}
                                            </AnimatePresence>
                                        </TableBody>
                                    </Table>
                                    {courses.filter(c => !c.isOngoing).length === 0 && (
                                        <div className="p-12 text-center text-slate-400 bg-slate-50/20">
                                            <CalcIcon className="w-12 h-12 mx-auto mb-3 opacity-20" />
                                            <p className="text-sm">No completed courses added yet.</p>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </TabsContent>

                        <TabsContent value="ongoing" className="p-6 focus-visible:ring-0">
                            <div className="flex flex-col gap-6">
                                <div className="bg-amber-50 border border-amber-100 p-4 rounded-lg flex items-start gap-3">
                                    <Info className="w-5 h-5 text-amber-600 mt-0.5 shrink-0" />
                                    <p className="text-sm text-amber-800">Assign your <strong>expected grades</strong> to see how they impact your standing.</p>
                                </div>
                                <div className="flex flex-col md:flex-row gap-4 p-4 bg-emerald-50/30 rounded-lg border border-emerald-100">
                                    <div className="flex-1 space-y-2">
                                        <Label htmlFor="o-name">Subject Name</Label>
                                        <Input id="o-name" value={newCourse.name} onChange={e => setNewCourse({ ...newCourse, name: e.target.value })} className="bg-white" />
                                    </div>
                                    <div className="w-full md:w-24 space-y-2">
                                        <Label htmlFor="o-credits">Credits</Label>
                                        <Input id="o-credits" type="number" value={newCourse.credits} onChange={e => setNewCourse({ ...newCourse, credits: parseInt(e.target.value) })} className="bg-white" />
                                    </div>
                                    <div className="w-full md:w-32 space-y-2">
                                        <Label htmlFor="o-grade">Expected</Label>
                                        <Select value={newCourse.grade} onValueChange={v => setNewCourse({ ...newCourse, grade: v as Grade })}>
                                            <SelectTrigger className="bg-white"><SelectValue /></SelectTrigger>
                                            <SelectContent>{Object.keys(GRADE_POINTS).map(g => (<SelectItem key={g} value={g}>{g}</SelectItem>))}</SelectContent>
                                        </Select>
                                    </div>
                                    <div className="flex items-end">
                                        <Button onClick={() => addCourse('ongoing')} className="bg-emerald-700 hover:bg-emerald-800 text-white w-full md:w-auto"><Plus className="w-4 h-4 mr-2" /> Add</Button>
                                    </div>
                                </div>
                                <div className="border rounded-xl bg-white overflow-x-auto">
                                    <Table>
                                        <TableHeader className="bg-slate-50"><TableRow><TableHead>Subject Name</TableHead><TableHead>Credits</TableHead><TableHead>Expected</TableHead><TableHead className="text-right">Action</TableHead></TableRow></TableHeader>
                                        <TableBody>
                                            <AnimatePresence mode="popLayout">
                                                {courses.filter(c => c.isOngoing).map(course => (
                                                    <motion.tr
                                                        key={course.id}
                                                        initial={{ opacity: 0, x: -20 }}
                                                        animate={{ opacity: 1, x: 0 }}
                                                        exit={{ opacity: 0, x: 20 }}
                                                        className="group border-b last:border-0 hover:bg-slate-50/50 transition-colors"
                                                    >
                                                        <TableCell className="font-medium py-3">{course.name}</TableCell>
                                                        <TableCell>{course.credits}</TableCell>
                                                        <TableCell>
                                                            <Select value={course.grade} onValueChange={v => updateCourse(course.id, { grade: v as Grade })}>
                                                                <SelectTrigger className="w-20 h-8 text-xs bg-white"><SelectValue /></SelectTrigger>
                                                                <SelectContent>{Object.keys(GRADE_POINTS).map(g => (<SelectItem key={g} value={g}>{g}</SelectItem>))}</SelectContent>
                                                            </Select>
                                                        </TableCell>
                                                        <TableCell className="text-right">
                                                            <Button variant="ghost" size="icon" onClick={() => removeCourse(course.id)} className="text-red-400 hover:text-red-600 hover:bg-red-50"><Trash2 className="w-4 h-4" /></Button>
                                                        </TableCell>
                                                    </motion.tr>
                                                ))}
                                            </AnimatePresence>
                                        </TableBody>
                                    </Table>
                                    {courses.filter(c => c.isOngoing).length === 0 && (
                                        <div className="p-12 text-center text-slate-400 bg-slate-50/20">
                                            <TrendingUp className="w-12 h-12 mx-auto mb-3 opacity-20" />
                                            <p className="text-sm">No ongoing subjects added. Add them to predict your CGPA.</p>
                                        </div>
                                    )}
                                </div>
                            </div>
                        </TabsContent>

                        <TabsContent value="planner" className="p-6 focus-visible:ring-0">
                            <div className="space-y-6">
                                <div className="bg-blue-50 border border-blue-100 p-4 rounded-lg flex items-start gap-3">
                                    <Target className="w-5 h-5 text-blue-600 mt-0.5 shrink-0" />
                                    <div>
                                        <h4 className="font-semibold text-blue-900">Scenario Analysis</h4>
                                        <p className="text-sm text-blue-800">Adjust grades in the &quot;Prediction&quot; tab to see changes. Add future subjects there to plan.</p>
                                    </div>
                                </div>
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-8 py-4">
                                    <div className="space-y-4">
                                        <h3 className="text-lg font-semibold text-slate-800">Target Planning</h3>
                                        <div className="p-6 bg-slate-50 rounded-2xl border border-slate-100 space-y-4">
                                            <div className="flex flex-col gap-2">
                                                <Label>Target CGPA</Label>
                                                <Input type="number" step="0.01" placeholder="e.g. 8.5" className="bg-white" onChange={(e) => {
                                                    const target = parseFloat(e.target.value);
                                                    const resultDisp = document.getElementById('target-result');
                                                    if (target > 0 && target <= 10 && resultDisp) {
                                                        const ongoingCr = courses.filter(c => c.isOngoing).reduce((s, c) => s + c.credits, 0);
                                                        if (ongoingCr > 0) {
                                                            const neededPts = target * (stats.current.totalCredits + ongoingCr) - stats.current.totalGradePoints;
                                                            const neededAvg = neededPts / ongoingCr;
                                                            resultDisp.innerText = neededAvg > 10 ? 'Impossible' : `Avg ${neededAvg.toFixed(2)} needed`;
                                                            resultDisp.className = neededAvg > 10 ? 'text-red-600 font-bold' : 'text-green-700 font-bold';
                                                        } else {
                                                            resultDisp.innerText = 'Add ongoing subjects';
                                                            resultDisp.className = 'text-slate-400 font-bold';
                                                        }
                                                    }
                                                }} />
                                            </div>
                                            <div className="flex justify-between items-center text-sm pt-2">
                                                <span>Result:</span>
                                                <span id="target-result" className="font-bold text-slate-400">---</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="space-y-4">
                                        <h3 className="text-lg font-semibold text-slate-800">Academic Standing</h3>
                                        <div className="p-6 rounded-2xl border bg-white space-y-3">
                                            <div className="flex justify-between text-sm items-center">
                                                <span className="text-slate-600">Status:</span>
                                                <span className={`px-2 py-1 rounded text-xs font-bold uppercase ${stats.current.cgpa >= 8 ? 'bg-green-100 text-green-700' :
                                                    stats.current.cgpa >= 6.5 ? 'bg-blue-100 text-blue-700' :
                                                        'bg-amber-100 text-amber-700'
                                                    }`}>
                                                    {stats.current.cgpa >= 8 ? 'Excellent' :
                                                        stats.current.cgpa >= 6.5 ? 'Good' :
                                                            'Satisfactory'}
                                                </span>
                                            </div>
                                            <div className="flex justify-between text-sm items-center">
                                                <span className="text-slate-600">Earned Credits:</span>
                                                <span className="font-semibold">{stats.current.totalCredits}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </TabsContent>
                    </Tabs>
                </CardContent>
            </Card>

            <div className="text-center text-slate-400 text-[10px] mt-8">
                <p>© Namdapha House inspired Academic Toolkit. Precision and Performance.</p>
                <p className="mt-1 italic">Disclaimer: This is a student tool. Always verify with official grade cards.</p>
            </div>
        </div>
    );
}
